import swal from "sweetalert";
export class APIControler {
    constructor(apiUrl = "http://45.162.168.170:8081", pathname = "proveedores/") {
    this.apiUrl = new URL(apiUrl);
    this.apiUrl.pathname = pathname;
  }

  addSearchParams(key, value) {
    this.apiUrl.searchParams.append(key, value);
  }

  deleteSearchParams(key) {
    this.apiUrl.searchParams.delete(key);
  }

  async getData() {
    try {
      let response = await fetch(this.apiUrl);
      let data = await response.json();

      return data.results;
    } catch (error) {
      console.log(error);
    }
  }

  async postData(payload) {
    try {
        console.log(JSON.stringify(payload))
      let response = await fetch(this.apiUrl, {
        method: "POST",
        headers: {
          "Accept": "application/json",
          "Content-Type": "application/json",
        },
        body: JSON.stringify(payload)
      });
      const content = await response.json();
      if(response.ok)
      {
        swal("Carga Exitosa", " ", "success")
        setTimeout(function () {
            window.location.reload();
        }, 1000);
        console.log(content)
      }
      else{
        console.log(content);
        swal("¡ERROR!", "Los datos no son válidos" , "error")
        return content;
      }
      //location.href = '/socios'
      
    } catch (error) {
      swal("¡ERROR!", "Se ha detectado un problema ", "error")
      console.log(error);
      //swal("¡ERROR!", "{error}", "error")
    }
    
    
  }

  async putData(payload) {
    try{
      let response = await fetch(this.apiUrl + payload.id, {
        method: "PUT",
        body: JSON.stringify(payload),
        headers: {
          "Accept": "application/json",
          "Content-Type": "application/json",
        },
      })
      console.log(JSON.stringify(payload));
    }catch (error) {

    }
  }

    async putData2(payload) {
        try {
            let response = await fetch(this.apiUrl + payload.id_stock, {
                method: "PUT",
                body: JSON.stringify(payload),
                headers: {
                    "Accept": "application/json",
                    "Content-Type": "application/json",
                },
            })
            console.log(JSON.stringify(payload));
        } catch (error) {

        }
    }
    async putData3(payload) {
        try {
            let response = await fetch(this.apiUrl + payload.id_producto + '/', {
                method: "PUT",
                body: JSON.stringify(payload),
                headers: {
                    "Accept": "application/json",
                    "Content-Type": "application/json",
                },
            })
            console.log(JSON.stringify(payload));
        } catch (error) {

        }
    }

    async putData4(payload) {
        try {
            let response = await fetch(this.apiUrl + payload.id_venta + '/', {
                method: "PUT",
                body: JSON.stringify(payload),
                headers: {
                    "Accept": "application/json",
                    "Content-Type": "application/json",
                },
            })
            console.log(JSON.stringify(payload));
        } catch (error) {

        }
    }
}
