import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import vueHeadful from 'vue-headful';

Vue.component('vue-headful', vueHeadful);

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: () => import("../components/Init.vue"),
    meta: { title: 'Home - Federación Tucumana de Mutuales' },
    webpackChunkName: "Home",
  },
  {
        path: "/Home",
        name: "Home",
        component: Home,
        meta: { title: 'Home - Federación Tucumana de Mutuales' },
        webpackChunkName: "Home",
  },
    {
        path: "/MainMobile",
        name: "Home",
        component: () => import("../components/MainMobile.vue"),
        meta: { title: 'Home - Federación Tucumana de Mutuales' },
        webpackChunkName: "Home",
    },
  {
    path: "/ventas",
    name: "Ventas",
    component: () => import("../views/ventas/Ventas.vue"),
    meta: { title: 'Ventas - Aranda Distribucion' }
  },
  {
    path: "/reportes",
    name: "Reportes",
    component: () => import("../components/Reportes.vue"),
    meta: { title: 'Reportes - Aranda Distribuciones' }
    },
    {
        path: "/reportesclientes",
        name: "Reportes Clientes",
        component: () => import("../views/reportes/Repartos_Clientes.vue"),
        meta: { title: 'Reportes Clientes - Aranda Distribuciones' }
    },
    {
        path: "/reportesventas",
        name: "Reportes Ventas",
        component: () => import("../views/reportes/Repartos_Ventas.vue"),
        meta: { title: 'Reportes Clientes - Aranda Distribuciones' }
    },
    {
        path: "/reportesrepartos",
        name: "Reportes Repartos",
        component: () => import("../views/reportes/Repartos_Repartos.vue"),
        meta: { title: 'Reportes Repartos - Aranda Distribuciones' }
    },
    {
        path: "/reportesrepartoscli",
        name: "Reportes Clientes de Repartos",
        component: () => import("../views/reportes/Repartos_RepartosClientes.vue"),
        meta: { title: 'Reportes Clientes de Repartos - Aranda Distribuciones' }
    },
    {
        path: "/reporteliquidacion",
        name: "Reportes Clientes de Repartos",
        component: () => import("../views/reportes/Liquidacion.vue"),
        meta: { title: 'Reportes Clientes de Repartos - Aranda Distribuciones' }
    },
    {
        path: "/reportesproductos",
        name: "Reportes de Productos",
        component: () => import("../views/reportes/Repartos_Productos.vue"),
        meta: { title: 'Reportes de Productos - Aranda Distribuciones' }
    },
  {
    path: "/login",
    name: "Login",
    component: () => import("../components/IniciarSesion.vue"),
    meta: {
      hideNavbar: true,
    }  
    },
    {
        path: "/login2",
        name: "Login2",
        component: () => import("../views/login/Login.vue"),
        meta: {
            hideNavbar: true,
        }
    },
  {
    path: "/eto",
    name: "Eto",
    component: () => import("../views/eto/Eto.vue"),
    meta: { title: 'Eto - Aranda Distribuciones' }
    },
    {
        path: "/pp",
        name: "Pp",
        component: () => import("../views/pp/Pp.vue"),
        meta: { title: 'Pp - Aranda Distribuciones' }
    },
    {
        path: "/informe",
        name: "Informe",
        component: () => import("../views/Informe/Informe.vue"),
        meta: { title: 'Informe - Aranda Distribuciones' }
    },
    {
        path: "/kc",
        name: "Kc",
        component: () => import("../views/kc/Kc.vue"),
        meta: { title: 'Kc - Aranda Distribuciones' }
    },
    {
        path: "/riego",
        name: "Riego",
        component: () => import("../views/riegos/Riego.vue"),
        meta: { title: 'Riego - Aranda Distribuciones' }
    },
    {
        path: "/campos",
        name: "Campos",
        component: () => import("../views/campos/Campos.vue"),
        meta: { title: 'Campos - Aranda Distribuciones' }
    },
    {
        path: "/cultivos",
        name: "Cultivos",
        component: () => import("../views/cultivos/Cultivos.vue"),
        meta: { title: 'Cultivos - Aranda Distribuciones' }
    },
    {
        path: '/usuarios',
        name: 'usuarios',
        component: () => import('../views/usuarios/Usuarios.vue'),
        meta: { title: 'usuarios - Aranda' }
    },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
