            },
            fechaactual()
            {
                const f = new Date();
                //Devuelve formato sólo de fecha pero en el formato regional actual ejemplo: 24/8/2019
                console.log(f.toLocaleDateString());

                /**
                 * Si no le convence, puede recurrir a hacer el ajuste manual
                 **/
                const formatDate = (d) => {
                    return d.getFullYear() + "-" + ('0' + (d.getMonth() + 1)).slice(-2) + "-" + ('0' + d.getDate()).slice(-2)
                }
                console.log(formatDate(f));
                return (formatDate(f));
            },
            getCultivo(id) {
                if (this.cultivos == []) {
                    return getCultivos().then(() => {
                        return this.getCultivo(id)
                    })
                }
                else {
                    return this.cultivos.find(s => {
                        return s.id_cultivo == id
                    })
                }
            },
            getStockactual(id_repa)
            {
                if (this.stocks == []) {
                    return getStocks().then(() => {
                        return this.getStockactual(id_repa)
                    })
                }
                else {
                    return this.stocks.find(s => {
                        if (s.fechastock == this.fechaactual())
                        {
                            return s.id_reparto.split('/')[4] == id_repa
                        }
                    })
                }
            },
            setCultivo()
            {
                this.$session.start()
                if (this.pagina.id_cultivo == null) {
                    this.$session.set('cultivused', null);
                } else
                {
                    this.$session.set('cultivused', this.getCultivo(this.pagina.id_cultivo.split('/')[4]));
                }
            },
